import React from 'react';
import Card from './components/Card';
import Cart from './components/Cart';
import Navbar from './components/Navbar';
import { connect } from 'react-redux';
import { getItems,  itemDetails, addCart, rmCart } from './redux/action';
import header from './images/1.png';
import Footer from './components/Footer';
import Modal from './components/Modal';

const CartCard=(props)=>{
    return(
    <div className="card" style={{width: "100%", marginTop:"2vh"}}>
        <div className="card-body">
            <div className="row">
                <div className="col">
                    <h5 className="card-title">{props.data.title}</h5>
                    <p className="card-text"><b>{props.data.price}</b></p>
                    <p className="card-text">Ket: {props.ket}</p>
                </div>
                <div className="col-lg-3">
                    <button className="btn btn-danger" onClick={props.rmCart}>Remove</button>
                </div>
            </div>
        </div>
    </div>   
    )
}

class Home extends React.Component{
    state={
        cart:0
    }
    componentDidMount(){
        this.props.getItems();
    }
    setModal(item){
        this.props.itemDetails(item);
    }
    showcart(){
        switch(this.state.cart){
            case 1:
                this.setState({cart:0});
                break;
            default:
                this.setState({cart:1})
        }
    }
    render(){
        return(
            <>
            <Navbar />
            <div className="container-fluid bg-dark" style={{background: "url(./img/bg.png)", textAlign:"center"}}>
                <img src={header} alt="header" style={{filter:"invert(90)",width:"80vw",maxWidth:"500px"}}/>
            </div>
            <Cart click={()=>this.showcart()} count={this.props.cart.length} />
            <div className="container-fluid">
                <div className="row justify-content-around">
                    {this.props.items.map(item=>{
                        return <Card item={item} click={()=>this.setModal(item)} key={item.title} />
                    })}
                </div>
            </div>
            <Footer />
            <Modal 
                cart={this.state.cart > 0 ? this.props.cart.map((item, i)=><CartCard data={item} ket={this.props.ket[i]} rmCart={()=>this.props.rmCart(i)} key={i} />) : ""} 
                addCart={()=>{this.props.addCart(this.props.selected)}} 
                close={()=>this.setState({cart:0})} />
            </>
        )
    }
}

const setProps=(state)=>{
    return{
        items: state.items,
        selected:state.selected,
        cart: state.cart,
        ket: state.ket
    }
}

export default connect(setProps, {getItems, itemDetails, addCart, rmCart})(Home);