import React from 'react';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDonate, faPlusCircle, faShoppingCart } from '@fortawesome/free-solid-svg-icons';
import { rmCart, rsCart, addKeterangan } from '../redux/action'

class Modal extends React.Component{
    state={
        keterangan:""
    }
    handleInput(e){
        this.setState({keterangan: e.target.value})
    }
    handleClose(e){
        this.props.close();
    }
    handleKet(e){
        this.props.addKeterangan(this.state.keterangan);
        this.props.addCart();
    }
    render(){
        return(
            <div className="modal fade" id="staticBackdrop" data-backdrop="static" data-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered modal-dialog-scrollable">
                    <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="staticBackdropLabel">{this.props.cart !== "" ? <div><FontAwesomeIcon icon={faShoppingCart} /> Keranjang</div> : this.props.item.title}</h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={this.props.close}>
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        {this.props.cart !== "" ? this.props.cart : <div dangerouslySetInnerHTML={{ __html: this.props.item.details }}></div>}
                    </div>
                    <div className="modal-footer justify-content-around">
                        {this.props.cart !=="" ? <button type="button" className="btn btn-danger" onClick={this.props.rsCart}>Reset</button> : ""}
                        {this.props.cart !=="" ? <button type="button" className="btn btn-warning" disabled={this.props.cartlength > 0 ? false : true}><FontAwesomeIcon icon={faDonate} /> Pembayaran</button> : <input className="form-control" onChange={this.handleInput.bind(this)} type="text" placeholder="Keterangan Beli" />}
                        {this.props.cart ==="" ? <button type="button" className="btn btn-warning" disabled={this.state.keterangan === "" ? true : false} onClick={this.handleKet.bind(this)} data-dismiss="modal"><FontAwesomeIcon icon={faPlusCircle} /> Add to Cart</button> : ""}
                        <button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={this.props.close}>Close</button>
                    </div>
                    </div>
                </div>
            </div>
        )
    }
}

const setProps=(state)=>{
    return {
        item: state.selected,
        cartlength: state.cart.length
    }
}

export default connect(setProps, { rmCart, rsCart, addKeterangan })(Modal);