import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlusCircle } from '@fortawesome/free-solid-svg-icons';

const Card=(props)=>{
    return(
        <div className="card" style={{width: "18rem", marginTop:"5vh"}}>
            <div className="card-img-top border-bottom" style={{borderColor:"rgba(0,0,0,.125)", height:"40vh", maxHeight:"400px", background: "URL("+props.item.image+") center"}}></div>
            <div className="card-body">
            <h5 className="card-title">{props.item.title}</h5>
            <p className="card-text"><b>{props.item.price}</b></p>
            <button className="btn btn-success" data-toggle="modal" data-target="#staticBackdrop" onMouseDown={props.click} ><FontAwesomeIcon icon={faPlusCircle} /> Add to Cart</button>
            </div>
        </div>
    )
}

export default Card;