const selectItem=(item={}, action)=>{
    switch(action.type){
        case "ITEM_DETAILS":
            return action.payload;
        default:
            return item;
    }
}

const cartReducer=(cart=[], action)=>{
    switch(action.type){
        case "ADD_CART":
            return [action.payload, ...cart];
        case "RM_CART":
            return cart.filter((item,i)=> i !== action.payload);
        case "RS_CART":
            return []
        default:
            return cart;
    }
}

const ketReducer=(ket=[], action)=>{
    switch(action.type){
        case "ADD_KET":
            return [action.payload, ...ket];
        case "RM_CART":
            return ket.filter((item,i)=> i !== action.payload);
        case "RS_CART":
            return [];
        default:
            return ket;
    }
}

export default combineReducers({
    items: itemsReducer,
    selected: selectItem,
    cart:cartReducer,
    ket: ketReducer
});