import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDonate, faShoppingCart } from '@fortawesome/free-solid-svg-icons';

const Cart=(props)=>{
    return(
        <div style={{width:"100%", textAlign:"center", justifyContent:"center", marginTop:"5vh"}}>
        <div className="btn-group" role="group" aria-label="Basic example">
            <button type="button" className="btn btn-danger" data-toggle="modal" data-target="#staticBackdrop" onClick={props.click}><FontAwesomeIcon icon={faShoppingCart} /> Keranjang: {props.count}</button>
            <button type="button" className="btn btn-warning" disabled={props.count > 0 ? false : true}><FontAwesomeIcon icon={faDonate} /> Pembayaran</button>
        </div>
        </div>
    )
}

export default Cart;