export const getItems=()=>{
    return {
        type: "GET_ITEMS"
    }
}

export const itemDetails=(item)=>{
    return{
        type:"ITEM_DETAILS",
        payload: item
    }
}

export const addCart=(item)=>{
    return{
        type:"ADD_CART",
        payload: item
    }
}

export const rmCart=(id)=>{
    return{
        type:"RM_CART",
        payload: id
    }
}

export const rsCart=()=>{
    return {
        type: "RS_CART"
    }
}

export const addKeterangan=(ket)=>{
    return{
        type: "ADD_KET",
        payload: ket
    }
}